/* !Annimation medium */
:root {
  --lower: 100;
  --upper: 700;
}
.navigation body {
  min-height: 100vh;
}
.navigation a.animated {
  width: auto;
  height: 50px;
  z-index:0;
  /*padding-top: 10%;*/
  display: block;
  padding-left: 10px;

  font-family: 'Roboto Mono', monospace;
  font-size: 7rem;
  white-space: nowrap;
  font-variation-settings: 'wght' 100;
  font-variation-settings: 'wght' var(--lower);
  margin: 0;
}
.navigation a.animated span {
  -webkit-animation: rise 2.25s infinite ease-in-out;
          animation: rise 2.25s infinite ease-in-out;
  -webkit-animation-delay: calc((var(--index) - 6) * 0.225s);
          animation-delay: calc((var(--index) - 6) * 0.225s);
  display: inline-block;
  vertical-align: top;
}
.navigation a.animated span {
  color:red;
  font-size: 18px !important;
}

@media only screen and (min-width: 1000px) {
  .navigation a.animated span {
    padding-top: 10%;
  }
}

.navigation a.animated span.span1 {
  --index: 0; --alpha-l: 0.5; --alpha-u: 1;
}
.navigation a.animated span.span2 {
  --index: 1; --alpha-l: 0.5; --alpha-u: 1;
}
.navigation a.animated span.span3 {
  --index: 2; --alpha-l: 0.5; --alpha-u: 1;
}
.navigation a.animated span.span4 {
  --index: 3; --alpha-l: 0.5; --alpha-u: 1;
}
.navigation a.animated span.span5 {
  --index: 4; --alpha-l: 0.5; --alpha-u: 1;
}
.navigation a.animated span.span6 {
  --index: 5; --alpha-l: 0.5; --alpha-u: 1;
}

@-webkit-keyframes rise {
  50% {
    font-variation-settings: 'wght' 700;
    font-variation-settings: 'wght' var(--upper);
    -webkit-transform: translate(0, -15%);
            transform: translate(0, -15%);
  }
}

@keyframes rise {
  50% {
    font-variation-settings: 'wght' 700;
    font-variation-settings: 'wght' var(--upper);
    -webkit-transform: translate(0, -15%);
            transform: translate(0, -15%);
  }
}
